import { Injectable } from '@angular/core';
import { RX_RECORD_DEFINITION, RxFieldDefinitionService } from '@helix/platform/record/api';
import { TranslateService } from '@ngx-translate/core';
import {
  IFormSectionBuilderConfig,
  RX_OVERLAY,
  RX_PERMISSION,
  RxOverlayService,
  Tooltip
} from '@helix/platform/shared/api';
import {
  BooleanFormControlComponent,
  CounterFormControlComponent,
  ExpressionFormControlComponent,
  ICheckboxFormControlOptions,
  ICounterFormControlOptions,
  IDefinitionPickerComponentOptions,
  IExpressionFormControlOptions,
  IPermissionEditorComponentOptions,
  ISelectFormControlOptions,
  ISwitcherFormControlOptions,
  ITextareaFormControlOptions,
  ITextFormControlOptions,
  RxDefinitionPickerComponent,
  RxDefinitionPickerType,
  RxPermissionEditorComponent,
  RxRevertCustomizationComponent,
  SelectFormControlComponent,
  SwitchFormControlComponent,
  TextareaFormControlComponent,
  TextFormControlComponent
} from '@helix/platform/shared/components';
import { IRecordDefinitionModel } from '../record-designer.types';
import { RecordCustomizationOptionsComponent } from './record-customization-options/record-customization-options.component';
import { RX_RECORD_DESIGNER } from './record-designer.constant';
import { RecordInheritanceEditorComponent } from './record-inheritance-editor/record-inheritance-editor.component';
import { SearchFieldEditorControlComponent } from './search-field-editor/search-field-editor-control.component';
import { ISearchFieldEditorControlOptions } from './search-field-editor/search-field.types';
import { findIndex, get, includes, isNumber } from 'lodash';
import { RecordDesignerExpressionConfigurator } from './record-designer-expression-configurator.class';
import { ArchiveAssociationsControlComponent } from './archive-associations-control/archive-associations-control.component';
import { IArchiveAssociationControlOptions } from './archive-associations-control/archive-associations-control.types';
import { RecordIndexesControlComponent } from './record-indexes-control/record-indexes-control.component';
import { IRecordIndexesControlOptions } from './record-indexes-control/record-indexes-control.types';
import { IRecordInheritanceSelectorOptions } from './record-inheritance-editor/record-inheritance-editor.interfaces';

@Injectable({
  providedIn: 'root'
})
export class RxRecordDesignerInspectorService {
  constructor(
    private rxFieldDefinitionService: RxFieldDefinitionService,
    private rxOverlayService: RxOverlayService,
    private translateService: TranslateService
  ) {}

  getDefinitionInspectorConfig(
    isNewDefinition: boolean,
    definitionModel: IRecordDefinitionModel,
    definitionModelFromDefinition: IRecordDefinitionModel,
    bundleId: string,
    isPropertiesCustomizationAllowed: boolean,
    isIndexCustomizationAllowed: boolean,
    isPermissionsCustomizationAllowed: boolean,
    isFieldsCustomizationAllowed: boolean,
    isReadOnly: boolean,
    expressionConfigurator: RecordDesignerExpressionConfigurator
  ): IFormSectionBuilderConfig[] {
    let configs: IFormSectionBuilderConfig[] = [];

    const isOverlayMode =
      !isNewDefinition && definitionModel.overlayOperation !== RX_OVERLAY.operationTypes.createdInThisOverlayGroup;
    const isAuditRecordDefinition = Boolean(definitionModel.isAuditRecordDefinition);

    const isDeleteSourceRecordOperation =
      definitionModel.archiveType === RX_RECORD_DESIGNER.archiving.types.deleteSourceRecord.id;

    const doNotArchive = definitionModel.archiveType === RX_RECORD_DESIGNER.archiving.types.doNotArchive.id;

    const archivingOptions = Object.values(RX_RECORD_DESIGNER.archiving.types).map((value) => ({
      ...value,
      name: this.translateService.instant(value.nameKey)
    }));

    const joinTypeOptions = Object.values(RX_RECORD_DEFINITION.joinTypes).map((joinType) => ({
      ...joinType,
      name: this.translateService.instant(joinType.displayName),
      id: joinType.value
    }));

    const weightedRelevancyFields = [
      {
        name: 'weightedRelevancyTitle',
        component: SelectFormControlComponent,
        isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
        options: {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.title-field.label'
          ),
          options: definitionModel.fields
            .filter(
              (fieldDefinition) =>
                isNumber(fieldDefinition.id) &&
                (get(fieldDefinition, 'searchDefinition.enableFTSSearch') ||
                  get(fieldDefinition, 'searchDefinition.enableCognitiveSearch')) &&
                !includes(
                  [definitionModel.weightedRelevancyEnvironment, definitionModel.weightedRelevancyKeywords],
                  fieldDefinition.id
                )
            )
            .map((fieldDefinition) => ({
              name: fieldDefinition.name,
              id: fieldDefinition.id
            })),
          tooltip: new Tooltip(
            this.translateService.instant(
              definitionModel.enableCognitiveSearch
                ? 'com.bmc.arsys.rx.client.record-designer.definition-properties.title-field-cognitive-search.tooltip'
                : 'com.bmc.arsys.rx.client.record-designer.definition-properties.title-field-fts.tooltip'
            )
          )
        } as ISelectFormControlOptions
      },
      {
        name: 'weightedRelevancyEnvironment',
        component: SelectFormControlComponent,
        isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
        options: {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.environment-field.label'
          ),
          options: definitionModel.fields
            .filter(
              (fieldDefinition) =>
                isNumber(fieldDefinition.id) &&
                (get(fieldDefinition, 'searchDefinition.enableFTSSearch') ||
                  get(fieldDefinition, 'searchDefinition.enableCognitiveSearch')) &&
                !includes(
                  [definitionModel.weightedRelevancyTitle, definitionModel.weightedRelevancyKeywords],
                  fieldDefinition.id
                )
            )
            .map((fieldDefinition) => ({
              name: fieldDefinition.name,
              id: fieldDefinition.id
            })),
          tooltip: new Tooltip(
            this.translateService.instant(
              'com.bmc.arsys.rx.client.record-designer.definition-properties.environment-field.tooltip'
            )
          )
        } as ISelectFormControlOptions
      },
      {
        name: 'weightedRelevancyKeywords',
        component: SelectFormControlComponent,
        isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
        options: {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.keywords-field.label'
          ),
          options: definitionModel.fields
            .filter(
              (fieldDefinition) =>
                isNumber(fieldDefinition.id) &&
                (get(fieldDefinition, 'searchDefinition.enableFTSSearch') ||
                  get(fieldDefinition, 'searchDefinition.enableCognitiveSearch')) &&
                !includes(
                  [definitionModel.weightedRelevancyTitle, definitionModel.weightedRelevancyEnvironment],
                  fieldDefinition.id
                )
            )
            .map((fieldDefinition) => ({
              name: fieldDefinition.name,
              id: fieldDefinition.id
            })),
          tooltip: new Tooltip(
            this.translateService.instant(
              'com.bmc.arsys.rx.client.record-designer.definition-properties.keywords-field.tooltip'
            )
          )
        } as ISelectFormControlOptions
      }
    ];

    if (definitionModel.resourceType === RX_RECORD_DEFINITION.recordDefinitionTypes.regular.recordDefinitionType) {
      configs = [
        {
          label: this.translateService.instant('com.bmc.arsys.rx.client.common.general-items.label'),
          controls: [
            {
              name: 'name',
              component: TextFormControlComponent,
              isDisabled: !isNewDefinition,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.name.label'),
                required: true
              } as ITextFormControlOptions
            },
            {
              name: 'description',
              component: TextareaFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.description.label'),
                rows: 3
              } as ITextareaFormControlOptions
            },
            {
              component: RxRevertCustomizationComponent,
              options: {
                overlayGroupId: definitionModel.overlayGroupId,
                overlayDescriptor: definitionModel.overlayDescriptor
              }
            },
            {
              name: 'customizationOptions',
              component: RecordCustomizationOptionsComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
              options: {
                definitionTypeDisplayName: this.translateService
                  .instant('com.bmc.arsys.rx.client.record-definition.label')
                  .toLowerCase(),
                recordDefinition: definitionModel
              }
            },
            {
              name: 'allowNonAdminToDeleteRecordInstances',
              component: SwitchFormControlComponent,
              isDisabled: isAuditRecordDefinition || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.allow-non-admin-users-to-delete-records.label'
                )
              } as ISwitcherFormControlOptions
            },
            {
              name: 'owner',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.owner.label')
              } as ITextFormControlOptions
            },
            {
              name: 'lastUpdateTime',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.modified-date.label')
              } as ITextFormControlOptions
            },
            {
              name: 'lastChangedBy',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.modified-by.label')
              } as ITextFormControlOptions
            },
            {
              name: 'permissions',
              component: RxPermissionEditorComponent,
              isDisabled: !isPermissionsCustomizationAllowed || isReadOnly,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.permissions.label'),
                type: RX_PERMISSION.editorContexts.record
              } as IPermissionEditorComponentOptions
            },
            {
              name: 'shouldExportData',
              component: SwitchFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isAuditRecordDefinition || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.should-export-data.label'
                ),
                tooltip: new Tooltip(
                  this.translateService.instant(
                    'com.bmc.arsys.rx.client.record-designer.definition-properties.should-export-data.tooltip'
                  )
                )
              } as ISwitcherFormControlOptions
            }
          ]
        },
        {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.record-inheritance-selector.title'
          ),
          controls: [
            {
              name: 'recordInheritanceSelector',
              component: RecordInheritanceEditorComponent,
              options: {
                recordDefinition: definitionModel,
                isReadOnly: isOverlayMode || !isPropertiesCustomizationAllowed || isAuditRecordDefinition
              } as IRecordInheritanceSelectorOptions
            }
          ]
        },
        {
          label: this.translateService.instant('com.bmc.arsys.rx.client.common.search.label'),
          controls: [
            {
              name: 'enableCognitiveSearch',
              component: SwitchFormControlComponent,
              isDisabled: isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.enable-cognitive-search.label'
                )
              } as ISwitcherFormControlOptions
            },
            {
              name: 'recordSearchFields',
              component: SearchFieldEditorControlComponent,
              options: {
                recordDefinitionModel: definitionModel,
                isReadOnly: !isPropertiesCustomizationAllowed || isAuditRecordDefinition || isReadOnly
              } as ISearchFieldEditorControlOptions
            },
            ...weightedRelevancyFields
          ]
        },
        {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.indexes.title'
          ),
          controls: [
            {
              name: 'indexDefinitions',
              component: RecordIndexesControlComponent,
              options: {
                isReadOnly: !isIndexCustomizationAllowed || isReadOnly,
                definitionModel
              } as IRecordIndexesControlOptions
            }
          ]
        },
        {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.display-value.title'
          ),
          controls: [
            {
              name: 'displayFieldIdInAssociation',
              component: SelectFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.associated-display-field.title'
                ),
                options: definitionModel.fields
                  .filter((field) => this.rxFieldDefinitionService.canBeAssociatedDisplayField(field))
                  .map((field) => ({ id: field.id, name: field.name }))
                  .sort((a, b) => a.name.localeCompare(b.name)),
                tooltip: new Tooltip(
                  this.translateService.instant(
                    'com.bmc.arsys.rx.client.record-designer.definition-properties.associated-display-field.tooltip'
                  )
                )
              } as ISelectFormControlOptions
            }
          ]
        },
        {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.auditing.title'
          ),
          controls: [
            {
              name: 'isAuditingEnabled',
              component: SwitchFormControlComponent,
              isDisabled: isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.enabled.label'
                )
              } as ISwitcherFormControlOptions
            },
            {
              name: 'auditRecordDefinitionName',
              component: TextFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.audit-record-definition-name.label'
                )
              } as ITextFormControlOptions
            },
            {
              name: 'auditSourceRecordDefinitionName',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.audited-from.label'
                )
              } as ITextFormControlOptions
            }
          ]
        },
        {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.archiving.title'
          ),
          controls: [
            {
              name: 'isArchivingEnabled',
              component: SwitchFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isOverlayMode || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.enabled.label'
                )
              } as ISwitcherFormControlOptions
            },
            {
              name: 'archiveType',
              component: SelectFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isOverlayMode || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.audit-record-operation.label'
                ),
                options: archivingOptions
              } as ISelectFormControlOptions
            },
            {
              name: 'includeAttachments',
              component: BooleanFormControlComponent,
              isDisabled:
                !isPropertiesCustomizationAllowed ||
                doNotArchive ||
                isOverlayMode ||
                isDeleteSourceRecordOperation ||
                isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.archiving.include-attachments.label'
                ),
                shouldDisplayAsCheckbox: true
              } as ICheckboxFormControlOptions
            },
            {
              name: 'archiveRecordDefinitionName',
              component: TextFormControlComponent,
              isDisabled:
                !isPropertiesCustomizationAllowed ||
                doNotArchive ||
                isOverlayMode ||
                isDeleteSourceRecordOperation ||
                isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.archive-record-definition-name.label'
                )
              } as ITextFormControlOptions
            },
            {
              name: 'ageQualifierInDays',
              component: CounterFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || doNotArchive || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.record-age.label'
                ),
                tooltip: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.archiving.record-age.tooltip'
                ),
                allowIntegerOnly: true
              } as ICounterFormControlOptions
            },
            {
              name: 'ageQualifierFieldId',
              component: SelectFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || doNotArchive || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.record-age-field.label'
                ),
                options: definitionModel.fields
                  .filter((field) => field.resourceType === RX_RECORD_DEFINITION.resourceTypes.dateTime)
                  .map((fieldDefinition) => ({
                    name: fieldDefinition.name,
                    id: fieldDefinition.id
                  }))
                  .sort((a, b) => a.name.localeCompare(b.name))
              } as ISelectFormControlOptions
            },
            {
              name: 'archiveDescription',
              component: TextFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || doNotArchive || isReadOnly,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.description.label')
              } as ITextFormControlOptions
            },
            {
              name: 'archiveDataCriteria',
              component: ExpressionFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || doNotArchive || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.archiving.record-filter.label'
                ),
                dataDictionary$: expressionConfigurator.getDataDictionary(RX_RECORD_DESIGNER.archiveDataCriteriaPath),
                operators: expressionConfigurator.getOperators(RX_RECORD_DESIGNER.archiveDataCriteriaPath),
                tooltip: new Tooltip(
                  this.translateService.instant(
                    'com.bmc.arsys.rx.client.record-designer.definition-properties.archiving.record-filter.tooltip'
                  )
                )
              } as IExpressionFormControlOptions
            },
            {
              name: 'associationsToFollowForArchive',
              component: ArchiveAssociationsControlComponent,
              options: {
                definitionModel,
                definitionModelFromDefinition,
                bundleId,
                isOverlayMode,
                isReadOnly: !isPropertiesCustomizationAllowed || doNotArchive
              } as IArchiveAssociationControlOptions
            }
          ]
        }
      ];
    }

    if (definitionModel.resourceType === RX_RECORD_DEFINITION.recordDefinitionTypes.join.recordDefinitionType) {
      configs = [
        {
          label: this.translateService.instant('com.bmc.arsys.rx.client.common.general-items.label'),
          controls: [
            {
              name: 'name',
              component: TextFormControlComponent,
              isDisabled: !isNewDefinition,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.name.label'),
                required: true
              } as ITextFormControlOptions
            },
            {
              name: 'description',
              component: TextareaFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.description.label'),
                rows: 3
              } as ITextareaFormControlOptions
            },
            {
              name: 'primaryRecordDefinitionName',
              component: RxDefinitionPickerComponent,
              isDisabled: true,
              options: {
                definitionType: RxDefinitionPickerType.Record,
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.primary-record.label'
                )
              } as IDefinitionPickerComponentOptions
            },
            {
              name: 'secondaryRecordDefinitionName',
              component: RxDefinitionPickerComponent,
              isDisabled: true,
              options: {
                definitionType: RxDefinitionPickerType.Record,
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.secondary-record.label'
                )
              } as IDefinitionPickerComponentOptions
            },
            {
              name: 'joinType',
              component: SelectFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.join-type.label'
                ),
                options: joinTypeOptions
              } as ISelectFormControlOptions
            },
            {
              name: 'joinCriteria',
              component: ExpressionFormControlComponent,
              isDisabled: isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.join-criteria.label'
                ),
                dataDictionary$: expressionConfigurator.getDataDictionary(RX_RECORD_DESIGNER.joinCriteriaPath),
                operators: expressionConfigurator.getOperators(RX_RECORD_DESIGNER.joinCriteriaPath)
              } as IExpressionFormControlOptions
            },
            {
              component: RxRevertCustomizationComponent,
              options: {
                overlayGroupId: definitionModel.overlayGroupId,
                overlayDescriptor: definitionModel.overlayDescriptor
              }
            },
            {
              name: 'customizationOptions',
              component: RecordCustomizationOptionsComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
              options: {
                definitionTypeDisplayName: this.translateService
                  .instant('com.bmc.arsys.rx.client.record-definition.label')
                  .toLowerCase(),
                recordDefinition: definitionModel
              }
            },
            {
              name: 'owner',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.owner.label')
              } as ITextFormControlOptions
            },
            {
              name: 'lastUpdateTime',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.modified-date.label')
              } as ITextFormControlOptions
            },
            {
              name: 'lastChangedBy',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.modified-by.label')
              } as ITextFormControlOptions
            },
            {
              name: 'permissions',
              component: RxPermissionEditorComponent,
              isDisabled: !isPermissionsCustomizationAllowed || isReadOnly,
              options: {
                label: this.translateService.instant('com.bmc.arsys.rx.client.common.permissions.label'),
                type: RX_PERMISSION.editorContexts.record
              } as IPermissionEditorComponentOptions
            },
            {
              name: 'shouldExportData',
              component: SwitchFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isAuditRecordDefinition || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.should-export-data.label'
                ),
                tooltip: new Tooltip(
                  this.translateService.instant(
                    'com.bmc.arsys.rx.client.record-designer.definition-properties.should-export-data.tooltip'
                  )
                )
              } as ISwitcherFormControlOptions
            }
          ]
        },
        {
          label: this.translateService.instant('com.bmc.arsys.rx.client.common.search.label'),
          controls: [
            {
              name: 'enableCognitiveSearch',
              component: SwitchFormControlComponent,
              isDisabled: isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.enable-cognitive-search.label'
                )
              } as ISwitcherFormControlOptions
            },
            {
              name: 'recordSearchFields',
              component: SearchFieldEditorControlComponent,
              options: {
                recordDefinitionModel: definitionModel,
                isReadOnly: !isPropertiesCustomizationAllowed || isAuditRecordDefinition || isReadOnly
              } as ISearchFieldEditorControlOptions
            },
            ...weightedRelevancyFields
          ]
        },
        {
          label: this.translateService.instant(
            'com.bmc.arsys.rx.client.record-designer.definition-properties.auditing.title'
          ),
          controls: [
            {
              name: 'isAuditingEnabled',
              component: SwitchFormControlComponent,
              isDisabled: isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.enabled.label'
                )
              } as ISwitcherFormControlOptions
            },
            {
              name: 'auditRecordDefinitionName',
              component: TextFormControlComponent,
              isDisabled: !isPropertiesCustomizationAllowed || isReadOnly,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.audit-record-definition-name.label'
                )
              } as ITextFormControlOptions
            },
            {
              name: 'auditSourceRecordDefinitionName',
              component: TextFormControlComponent,
              isDisabled: true,
              options: {
                label: this.translateService.instant(
                  'com.bmc.arsys.rx.client.record-designer.definition-properties.audited-from.label'
                )
              } as ITextFormControlOptions
            }
          ]
        }
      ];
    }

    if (isOverlayMode) {
      configs[0]?.controls.splice(findIndex(configs[0].controls, { name: 'shouldExportData' }), 1);
    }

    if (isNewDefinition) {
      configs[0]?.controls.splice(findIndex(configs[0].controls, { name: 'owner' }), 3);
    }

    return configs;
  }
}
